@use '../../../style/variables.scss' as v;

.json_ihm {

    display: flex;
    flex-direction: column;

    .section {

        margin: 10px 0;

        &:first-child {
            margin-top: 0px;
        }

    }

    .section_title {
        cursor: pointer;
        color: v.$contrast_color;
        font-size: 1.2em;
        margin-bottom: 10px;

        & span:first-child {
            margin-right: 10px;
            font-size: 1.3em;
        }
    }

    .section_body {
        // background-color: v.$section_background_color;
        background-color: rgba(v.$secondary_color, 0.5);
        border: solid 1px v.$secondary_color;
        overflow: hidden;
        transition: max-height 0.5s, padding 0.5s;
        padding: 10px;

        &.expand { 
            height: auto;
            max-height: 1200px;
        }

        &.collapse {
            padding: 0px;
            max-height: 0px;
            border: none;
        }

        &#data_section,
        &#camera_move_section,
        &#data_process_section,
        &#object_section,
        &#action_section{

            .card_group {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
            }

            .section_footer {
                // margin-top: 10px;
                display: flex;
                flex-direction: row;
                justify-content: right;
            }

        }

    }

    .card {
        background: v.$contrast_color;
        margin-right: v.$card_margin_right;
        margin-bottom: v.$card_margin_bottom;
        padding: v.$card_padding;
        transition: box-shadow 0.1s;
        box-shadow: v.$card_box_shadow;
        border-radius: v.$card_border_radius;

        &:hover {
            box-shadow: v.$card_box_shadow_hover;
        }

        &:active {
            box-shadow: v.$card_box_shadow_active;
        }

        // &:last-child {
        //     margin-right: 0px;
        // }
        .card_content {
            display: flex;
            flex-direction: row;
            height: 100%;
            width: 100%;
            justify-content: space-between;
        }
        
        &.overview {
            
            height: v.$card_overview_height;
            width: v.$card_overview_width;

            .card_content_left {
                color: darken(v.$secondary_color, 10);
                display: flex;
                flex-direction: column;
                justify-content: start;
                align-items: start;
                margin-left: 10px;
        
            }

            .card_content_right {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                margin-left: 10px;

            }

        }

        &.modification {

            // height: v.$card_overview_height;
            &.card_data
            {
                width: calc( v.$card_overview_width * 2 + v.$card_padding * 2 + v.$card_margin_right * 1 );
                
                .card_content_left {
                    grid-template-areas: "a a";
                    grid-auto-columns: 1fr 1fr;
                    grid-auto-rows: 45px;
                    grid-auto-flow: row dense;
                    grid-gap: 10px;
                }
            }

            &.card_object,
            &.card_action,
            &.card_move,
            &.card_dataProcess
            {
                width: calc( v.$card_overview_width * 3 + v.$card_padding * 4 + v.$card_margin_right * 2 );
                
                .card_content_left {
                    grid-template-areas: "a a a";
                    grid-auto-columns: 1fr 1fr 1fr;
                    grid-auto-rows: v.$input_row_size;
                    grid-auto-flow: row dense;
                    grid-gap: v.$input_row_gap;
                }
            }

            .card_content_left {

                width: 100%;
                display: grid !important;
        
                label {
                    grid-row: span 1;
                }
            }

            .card_content_right {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                margin-left: 10px;

            }

        }

        button#collapse{
            // height: calc( 3 * v.$icon_button_height );
        }

        .input {

            &_space{
                grid-row: span 1;
            }

            &_string{
                grid-row: span 1;
            }

            &_number{
                grid-row: span 1;
            }

            &_boolean{
                grid-row: span 1;
            }

            &_color{
                grid-row: span 1;
            }

            &_select{
                grid-row: span 1;
            }

            &_position{
                grid-row: span 3;
            }

            &_array{
                
                &[id="Data"],
                &[id="DataProcessing"],
                &[id="String"]{
                    grid-row: span 3;
                }

                &[id="Color %"] {
                    grid-row: span 3;
                }
                
                &[id="Number %"] {
                    grid-row: span 3;
                }
            }

        }

    }

}