@use '../../../../style/variables.scss' as v;

#data_process_section {

    .card {

        &.modification {

            .card_content_left {

                display: grid;
                grid-template-columns: auto auto;
                grid-template-rows: auto auto;
                grid-template-areas: 
                    "name type"
                    "data properties";
                gap: 10px;
                // justify-content: space-around; 

                label#type{
                    grid-area: type;
                    align-self: center;
                    // justify-self: stretch
                }

                label#name{
                    align-self: center;
                    grid-area: name;
                }

                .left {
                    grid-area: data;
                    margin-right: 10px;
                    display: grid;
                    grid-template-columns: 15px 1fr 25px;
                    grid-template-rows: repeat(8,auto);
                    grid-template-areas: 
                        "init init ."
                        ". initInput ."
                        "add add ."
                        ". addArea addArea"
                        ". addInput addBtn"
                        "sub sub ."
                        ". subArea subArea"
                        ". subInput subBtn";
                    // gap: 10px;

                    .init{
                        grid-area: init;
                    }

                    .initInput{
                        grid-area: initInput;
                    }

                    .add{
                        margin-top: 10px;
                        grid-area: add;
                    }

                    .addArea{
                        display: flex;
                        flex-direction: column;
                        grid-area: addArea;
                    }

                    .addInput{
                        grid-area: addInput;
                    }

                    .addBtn{
                        grid-area: addBtn;
                    }

                    .sub{
                        margin-top: 10px;
                        grid-area: sub;
                    }
                    .subArea{
                        display: flex;
                        flex-direction: column;
                        grid-area: subArea;
                    }

                    .subInput{
                        grid-area: subInput;
                    }

                    .subBtn{
                        grid-area: subBtn;
                    }
                }

                .right{
                    grid-area: properties;

                    .density {
                        display: flex;

                        input {
                            width: 80px;
                        }

                        label {
                            margin-right: 10px;

                            &:last-child{
                                margin-right: 0px;
                            }
                        }
                    }

                    .latitude_limits,
                    .longitude_limits {
                        display: flex;
                        align-items: start;

                        label{
                            flex-grow: 4;

                            &:first-child{
                                margin-right: 10px;
                            }

                            &:last-child{
                                margin-left: 10px;
                            }
                        }
                    }
                }

            }

        }

    }

}