@use 'variables.scss' as v;


.input {

    &_string,
    &_number,
    &_color,
    &_select,
    &_boolean {
        // color:blue;
        position: relative;
        border: solid 1px rgb(176, 176, 176);
        border-radius: 3px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        height: 32px;
        font-size: 1em;

        & > span {
            position: absolute;
            top: -13px;
            left: 4px;
            background-color: v.$contrast_color;
            width: fit-content;
            padding: 0 5px;
            z-index: 2;
        }

        input {
            border: none;
            // border-bottom: solid 1px rgb(176, 176, 176);
            background-color: rgba(254, 254, 254, 0);
            outline: none;
            width: 90%;
        }
    }

    &_number{
    }

    &_boolean{
    }

    &_color{
    }

    &_select{
        
        select {
            border: none;
            background-color: rgba(254, 254, 254, 0);
            outline: none;
            width: 90%;
        }
    }

    &_position{
        // color:blue;
        position: relative;
        border: solid 1px rgb(176, 176, 176);
        border-radius: 3px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        // height: 32px;
        font-size: 1em;

        & > span {
            position: absolute;
            top: -13px;
            left: 4px;
            background-color: v.$contrast_color;
            width: fit-content;
            padding: 0 5px;
            z-index: 2;
        }

        label {
            position: relative;
            border: solid 1px rgb(176, 176, 176);
            border-radius: 3px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 10px;
            height: 32px;
            font-size: 1em;
    
            span {
                position: absolute;
                top: -13px;
                left: 4px;
                background-color: v.$contrast_color;
                width: fit-content;
                padding: 0 5px;
                z-index: 2;
            }
        }

        input {
            border: none;
            // border-bottom: solid 1px rgb(176, 176, 176);
            background-color: rgba(254, 254, 254, 0);
            outline: none;
            width: 90%;
        }
    }

    &_array{
        
        &[id="Data"],
        &[id="DataProcessing"],
        &[id="String"]{
            
            position: relative;
            border: solid 1px rgb(176, 176, 176);
            border-radius: 3px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            margin-top: 10px;
            // height: 100%;
            padding: 6px 0;
            font-size: 1em;

            & > span {
                position: absolute;
                top: -13px;
                left: 4px;
                background-color: v.$contrast_color;
                width: fit-content;
                padding: 0 5px;
                z-index: 2;
            }

            .input_array_list {
                width: 100%;
                overflow-y: scroll;
                height: 105px;
                display: flex;
                flex-direction: column;
                // justify-content: center;
                align-items: center;

                & > div {
                    width: 90%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin: 2px 0;
                }


                select {
                    border: none;
                    background-color: rgba(254, 254, 254, 0);
                    outline: none;
                    width: 80%;
                    border-bottom: solid 1px rgb(176, 176, 176);
                }
            }

        }

        &[id="Color %"],
        &[id="Number %"]  {
            position: relative;
            border: solid 1px rgb(176, 176, 176);
            border-radius: 3px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            margin-top: 10px;
            // height: 100%;
            padding: 6px 0;
            font-size: 1em;

            & > span {
                position: absolute;
                top: -13px;
                left: 4px;
                background-color: v.$contrast_color;
                width: fit-content;
                padding: 0 5px;
                z-index: 2;
            }

            .input_array_list {
                width: 100%;
                overflow-y: scroll;
                height: 105px;
                display: flex;
                flex-direction: column;
                align-items: center;

                & > div {
                    width: 90%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin: 2px 0;
                }

                input {
                    border: none;
                    background-color: rgba(254, 254, 254, 0);
                    outline: none;

                    &#percent {
                        width: 45px;
                        border-bottom: solid 1px rgb(176, 176, 176);
                    }

                    &#value {
                        width: 45px;
                    }
                }
            }
        }
        
        &[id="Number %"] {

            .input_array_list {

                input {

                    &#value {
                        border-bottom: solid 1px rgb(176, 176, 176);
                    }
                }
            }
        }
    }

}