@use '../../../style/variables.scss' as v;



#video-fps-select {
    // -webkit-appearance: none;
    // -moz-appearance: none;
    // appearance: none;
    // display: inline-block;
    margin: 5px;
    padding: 5px;
    cursor: pointer;
    color: rgb(70, 81, 88);
    font-family: inherit;
    font-size: 16px;
    border: solid grey 1px;
    border-radius: 3px;
    background-color: rgb(250, 250, 250);
    background-size: 10px;
    background-repeat: no-repeat;
    background-position: calc(100% - 5px) center;
}

.video-input {
   margin: 5px;
   width: 50px;
   border-radius: 3px;
   font-family: inherit;
   font-size: 18px;
}

#video_section {
    display: flex;
    gap: 10px;
    align-content: flex-start;
}

.video-scd-input{
    margin-left: 50px;
}

.video-custom-input {
    border: solid grey 1px;
    padding: 5px;
    border-radius: 3px;
    background-color: rgb(183, 183, 183);
}

