@use '../../style/variables.scss' as v;

// h1 {
//     color: variables.$background-color
// }

.json_generator {

    width: 100%;
    min-height: v.$content_min_height;
    height: 100%;
    // flex: 1;
    display: flex;
    flex-direction: column;

    .json_head {
        // flex: 0;
        margin: 20px 0;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;

        .switch{
            height: 30px;
            width: 120px;
            padding: 0 10px;
            border-radius: 15px;
            border: none;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            position: relative;
            color: v.$primary_color;
            background-color: v.$contrast_color;
            border: solid 1px #fff;

            &::after{
                content: '';
                position: absolute;
                top: 0;
                height: 28px;
                border-radius: 15px;
                background-color: v.$secondary_color;
                z-index: 0;
                transition: left 0.3s, width 0.3s;
            }

            &.left {
                &::after{
                    left: 0;
                    width: 65px;
                }

                span.left {
                    color: v.$accent_color;
                }
            }

            &.right {
                &::after{
                    left: 68px;
                    width: 50px;
                }

                span.right {
                    color: v.$accent_color;
                }
            }

            span {
                z-index: 2;
            }
        }
    }

    .json_ihm {
        // flex: 1;
    }



    .jsoneditor-react-container {
        width: 100%;
        height: 100%;
        flex: 1;
        display: table;

        .jsoneditor {
            // display: table-row;
        }

    }

    .buttons_template {
        width: 100%;;
        margin: 10px 0 20px 0;
        display: flex;
        justify-content: end;

        .text_button {
            margin-left: 10px;
        }

        &.hide{
            display: none;
        }

    }

}