@use '../../style/variables.scss' as v;

.template {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: v.$content_min_height;
    height: 100%;
    // padding: 10px;

    .title {
        margin: 20px 0;
    }
    // & > span{
    //     font-family: v.$font; 
    //     font-size: 2rem;
    //     margin: 20px 0;
    // }
        
    .template_container {
        // background-color: v.$section_background_color;
        // overflow: hidden;
        height: 100%;
        transition: max-height 0.5s, padding 0.5s;
        padding: 10px;
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        flex-wrap: wrap;
        // margin: 0px 10px 10px 10px;

        .card {
            
            // overflow: hidden;
            background: v.$card_background_color;
            margin-right: v.$card_template_margin_right;
            margin-bottom: v.$card_template_margin_bottom;
            // padding: v.$card_padding;
            transition: box-shadow 0.1s;
            box-shadow: v.$card_box_shadow;
            border-radius: v.$card_border_radius;
            height: v.$card_template_height;
            width: v.$card_template_width;
            position: relative;
            background-size: cover;
            cursor:pointer;
            transition: all 0.3s;
            display: flex;
            flex-direction: column;
            justify-content: end;

            &.selected {
                height: calc( v.$card_template_height * 1.2 );
                width: calc( v.$card_template_width * 1.2 );
                box-shadow:
                    rgba(white, 0.2) 0 0 6px 5px,
                    rgba(white, 1) 0 0 0 1px,
                    v.$card_box_shadow_hover;

                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    height: 8px;
                    width: 8px;
                    border-radius: 50%;
                    bottom: -25px;
                    left: calc( 50% - 8px );
                    // background-color: #fff;
                    border: solid 4px #fff;
                    box-shadow:
                        rgba(white, 0.2) 0 0 6px 5px;
                    transition: all 0.3s;
                }
            }

            &:before {
                content: '';
                position: absolute;
                height: 0px;
                width: 0px;
                border-radius: 50%;
                bottom: -17px;
                left: calc( 50% - 0px );
                // background-color: #fff;
                border: solid 0px #fff;
                box-shadow:
                    rgba(white, 0.2) 0 0 0px 0px;
                transition: all 0.3s;
            }

            &:after {
                content: '';
                position: absolute;
                height: calc( 100% - 12px );
                width: calc( 100% - 12px );
                top: 5px;
                left: 5px;
                // background-color: red;
                border: solid 1px #fff;
                border-radius: calc( v.$card_border_radius / 2 );
            }

            &:hover {
                            
                box-shadow:
                    rgba(white, 0.2) 0 0 6px 5px,
                    rgba(white, 1) 0 0 0 1px,
                    v.$card_box_shadow_hover,
                // inset #333 0 0 0 5px,
                // inset white 0 0 0 6px;
                // border: -1px solid #fff;
                // box-shadow: v.$card_box_shadow_hover;
                // box-shadow: rgb(255, 255, 255) 0px 0px 10px,
                // v.$card_box_shadow_hover;

            }

            &:active {
                box-shadow: v.$card_box_shadow_active;
            }

            & > img {
                z-index: 0;
                height: calc( 100% + v.$card_padding * 2);
                position: absolute;
                left: calc(0px - v.$card_padding );
                top: calc(0px - v.$card_padding );
            }

            .info_container {
                overflow: hidden;
                margin: 6px;
                height: 40px;
                width: calc( 100% - 12px );
                display: flex;
                flex-direction: column;
                // justify-content: center;
                background-color: rgba(0, 0, 0, 0.666);
                align-items: center;
                z-index: 2;
                transition: height 0.4s;

                &:hover {
                    height: 150px;
                }

                span {
                    width: fit-content;
                    color: white;
                    // z-index: 2;
                    font-family: v.$font; 

                    &.name {
                        margin: 10px 0 15px;
                        font-size: 1.2em;
                    }

                    &.separateur {
                        width: 60px;
                        height: 2px;
                        background-color: #fff;
                    }

                    &.desc {

                        margin: 10px;
                        
                    }
                }
            }

        }
    }

    .template_needs {
        

        .section {

            margin: 10px 0;

            &:first-child {
                margin-top: 0px;
            }

        }

        .section_title {
            cursor: pointer;
            color: v.$contrast_color;
            font-size: 1.2em;
            margin-bottom: 10px;

            & span:first-child {
                margin-right: 10px;
                font-size: 1.3em;
            }
        }

        .section_body {
            // background-color: v.$section_background_color;
            background-color: rgba(v.$secondary_color, 0.5);
            border: solid 1px v.$secondary_color;
            overflow: hidden;
            transition: max-height 0.5s, padding 0.5s;
            padding: 10px;

            .card_group {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
            }

            &#dataT_section{

                .section_footer {
                    // margin-top: 10px;
                    display: flex;
                    flex-direction: row;
                    justify-content: right;
                }

            }

        }

        
        .card {
            background: v.$contrast_color;
            margin-right: v.$card_margin_right;
            margin-bottom: v.$card_margin_bottom;
            padding: v.$card_padding;
            transition: box-shadow 0.1s;
            box-shadow: v.$card_box_shadow;
            border-radius: v.$card_border_radius;
            
            height: v.$card_overview_height;
            width: v.$card_overview_width;

            .card_content {
                display: flex;
                flex-direction: row;
                height: 100%;
                width: 100%;
                justify-content: space-between;

                .card_content_left {
                    color: darken(v.$secondary_color, 10);
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: start;

                    label {
                        width: 100%;
                    }
                    // margin-left: 10px;
                }

                .card_content_right {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    margin-left: 10px;
                }

            }

        }
        
        .card_AreaT,
        .card_DataObjectT {
            margin-top: 10px;
            margin-bottom: 10px;
            height: calc( v.$card_overview_height * 1.7 );
            width: calc( v.$card_overview_width * 1 );
            background-size: cover;
            cursor:pointer;

            .card_content {
                display: flex;
                flex-direction: column;
                justify-content: end;
            }
                
            &:hover {
                box-shadow: v.$card_box_shadow_hover;
            }

            &:active {
                box-shadow: v.$card_box_shadow_active;
            }

            span {
                color: white;
                width: 100%;
                height: 30px;
                padding-top: 10px;
                text-align: center;
                background-color: #000000be;

            }

            &.active {
                box-shadow:
                    rgba(white, 0.2) 0 0 6px 5px,
                    rgba(white, 1) 0 0 0 1px,
                    v.$card_box_shadow_hover;
            }

        }

    }

    .buttons_template {
        width: 100%;;
        margin: 10px 0 20px 0;
        display: flex;
        justify-content: end;

        .text_button {
            margin-left: 10px;
        }

        &.hide{
            display: none;
        }

    }

}
